#app-title {
  font-size: 1.25rem;
  margin: auto;
}

#credit-name {
  color: #9183FA;
}

.main-div {
  width: 90%;
  margin: 1rem auto;
  background-color: #363636;
  border-radius: 5px;
  color: #fafafa;
}

.main-div-header {
  background-color: #6153CA;
  padding: .025rem;
  padding-left: .4rem;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
}

.main-div h3 {
  margin: .3rem 0;
  font-size: 1.1rem;
}

#HeaderWrapper {
  text-align: center;
  padding-top: 1rem;
  color: #fafafa;
}

#EditorWrapper, #PreviewerWrapper {
  border: 1px solid black;
}

#editor {
  color: #fff;
  width: 98%;
  height: 35vh;
  margin: auto;
  margin-bottom: -.2rem;
  background-color: #474747;
  border: 1px solid #585858;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
}

#preview {
  height: 35vh;
  font-size: .9em;
  padding-left: 0.5rem;
  margin: auto auto .4rem auto;
  overflow: auto;
}

pre, code {
  background-color: #696969;
  border-radius: 3px;
}

pre {
  overflow: auto;
  margin-right: .5rem;
}

blockquote {
  border-left: 2px solid #fafafa;
  margin-left: .8rem;
}

table, th, td {
  border: 1px solid #fafafa;
  border-collapse: collapse;
}

th, td {
  padding: .2rem;
}

blockquote > * {
  padding-left: .5rem;
}

@media only screen and (min-width: 400px) {
  #editor {
    width: 99%;
  }
}

@media only screen and (min-width: 768px) {
  #editor {
    width: 99.5%;
  }

  #app-title {
    font-size: 1.8rem;
  }

  #credit {
    font-size: 1.2rem;
  }

  #HeaderWrapper {
    margin-bottom: 2rem;
  }

  #EditorWrapper, #PreviewerWrapper {
    width: 45%;
    float: left;
    margin-left: 3.33%;
  }

  #editor, #preview {
    height: 78vh;
  }

  .main-div h3 {
    line-height: 2rem;
  }
}